import React, { Component } from "react";
import { Container, FormControl, Nav, Navbar, Form, Button } from "react-bootstrap";
import NavbarCollapse from "react-bootstrap/esm/NavbarCollapse";
import NavbarToggle from "react-bootstrap/esm/NavbarToggle";
import logo from './logo192.png'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Route, Routes, NavLink, Outlet, Link } from "react-router-dom";
import Home from "../pages/Home";
import About from "../pages/About";
import Contacts from "../pages/Contacts";
import Blog from "../pages/Blog";

export default class Header extends Component {
    render() {
        return (
            <div>
    <Navbar fixed="top" collapseOnSelect expand="md" bg="dark" variant="dark">
                    <Container>
                        <Navbar.Brand href="/">
                            <img src={logo} height='30' width='30' className='d-inline-block align-top' />
                        </Navbar.Brand>

                        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                        <Navbar.Collapse id="responsive-navbar-nav">
                            <Nav className="me-auto" >
                                <NavLink to="/">Home</NavLink>
                                <NavLink to="/about">About Us</NavLink>
                                <NavLink to="/contacts">Contacts</NavLink>
                                <NavLink to="/blog">Blog</NavLink>
                            </Nav>
                            <Form className="d-flex">
                                <FormControl type="text" placeholder="Search" className="d-inline mx-2"></FormControl>
                                <Button variant="outline-info">Seacrh</Button>
                            </Form>
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
                <Routes>
                    <Route path='/' element={<Home/>} />
                    <Route path='about' element={<About />} />
                    <Route path='contacts' element={<Contacts />} />
                    <Route path='blog' element={<Blog />}  />
                </Routes>
            </div>

        );
    }
}

