import React from "react"
import { Carousel } from "react-bootstrap";
import forest from "../assets/fores4k.jpg";
import forestTwo from "../assets/forest4k2.jpg";
import 'bootstrap/dist/css/bootstrap.min.css';
import "./CarouselBox.css";


const CarouselBox = () => {
    return (
        <div className="CarouselBox">
            <Carousel>
                <Carousel.Item >
                  <img src={ forest } alt="" className="d-block blogg-img"/>  
                  <Carousel.Caption>
                      <h3>Forest image</h3>
                      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Suscipit, obcaecati!</p>
                  </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item >
                  <img src={ forestTwo } alt="" className="d-block blogg-img"/>  
                  <Carousel.Caption>
                      <h3>Forest image Light</h3>
                      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Suscipit, obcaecati!</p>
                  </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                  <img src={ forest } alt="" className="d-block blogg-img"/>  
                  <Carousel.Caption>
                      <h3>Forest image</h3>
                      <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Suscipit, obcaecati!</p>
                  </Carousel.Caption>
                </Carousel.Item>
                </Carousel>
            </div>
    )
}
export default CarouselBox;